img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

ul , h1 , p , h3{
    margin: 0;
    padding: 0;
    list-style: none;
}

a{
    display: block;
    cursor: pointer; //防止部分card區域a連結cursor: pointer;失效

    &:active{
      color: shift-color($white, 0%)
    }
}

